/** @format */

.Chats {
  display: flex;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
}
.Chats:hover {
  background-color: white;
}
h2 {
  margin: 0;
  font-size: 16px;
}
a {
  text-decoration: none;
  color: black;
}
p {
  margin: 0;
  margin-bottom: 8px;
}

.chat_info {
  margin-left: 15px;
}
.sidebarChat h2 {
  margin-left: 25px;
  font-size: 25px;
  cursor: pointer;
}
