/** @format */

.sidebar {
  flex: 0.35;
}

.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border: 1px solid lightgray;
}
.sidebarHeader__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
}
.sidebar__search {
  padding: 10px;
  align-items: center;
  display: flex;
  background-color: #f6f6f6;
  height: 39px;
}

.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}
.sidebar__searchContainer > input {
  border: none;
  flex: 1;
  margin-left: 10px;
}
.sidebar__searchContainer > .MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}
.sidebar__chats {
  flex: 1;
}
