.conversation{
    flex: .65;
    display: flex;
    flex-direction: column;
}
.convo__header{
    padding: 20px;
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
}
.header__info h3{
    margin: 0%;
    margin-bottom: 1.5px;
    font-weight: 500;
}
.header__info p{
    margin: 0%;
    color: gray;
    
}
.header__info {
    flex: 1;
    padding-left: 20px;
}
.header__icon{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}
.convo__body{
    flex:1;
    background-image: url("https://www.setaswall.com/wp-content/uploads/2019/08/Whatsapp-Wallpaper-122-768x1365.jpg");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: scroll;

}
.chat_message{
    position: relative;
    background-color: white;
    font-size: 16px;
    width: fit-content;
    margin-bottom: 30px;
    border-radius: 10px;
    padding: 10px;
}
.sender__name{
    position: absolute;
    top: 0px;
    font-size: xx-small;
    font-weight: 800;
}
.time{
    margin-left: 10px;
    font-size: xx-small;
}
.chat__reciver{
 margin-left: auto;
 background-color: #dcf8c6;
}
.convo__typer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid gray;
}
.convo__typer >form{
    flex: 1;
    display: flex;
}
.convo__typer >form > input
{
    flex: 1;
    border-radius: 20px;
}
.convo__typer >form > button{
    font-size: 8px;
    border-radius: 50px;
    width: 35px;
}
